$(".filter__toggle").click(function () {
    $(this).parents(".filter__group").toggleClass("_active")
    $(this).parents(".filter__group").find(".filter__hidden").slideToggle()
})

$(".catalog__show-filter").click(function () {
    $(".filter").addClass("_open")
    $("body").addClass("fixed-body")
})

$(".filter__bg").click(function () {
    $(".filter").removeClass("_open")
    $("body").removeClass("fixed-body")
})

$(".rating-input__item").click(function() {
    let ratingItem = parseInt($(this).attr("data-rating-value"))
    $(this).parent(".rating-input").attr("data-total-rating", ratingItem)
    $(this).siblings(".rating-input__field").val(ratingItem)
})

$(".product-props__link").click(function () {
    $(".product-props__link").removeClass("_active")
    $(this).addClass("_active")
})
